@import "/src/styles/colors";

.card {

  .card-container {
    background: $color-chat;
    border-radius: 20px;
    margin-top: 10%;
    width: 400px;

    input {
      color: $color-text;
    }

    p, span {
      color: $color-scrollbar-foreground;
    }

    .stack-header {
      justify-content: center;
      align-items: center;

      .stack-title {
        align-items: center;
        color: $color-text;
        font-size: 25px;
      }

      .button-question {
        background: none;
        border-radius: 50%;
        margin: 0 0 0 10px;
        padding: 0 0 0 0;

        &:hover {
          border-radius: 50%;
          background: $color-button-hover;
        }
      }

      .info {
        font-size: 16px;
        font-weight: 400;
        background: $color-background;
        color: $color-text;
      }
    }
  }
}
