@import '/src/styles/colors';

.card {
  width: 100%;

  .container-header {
    position: relative;
    height: calc(90px - 24px);
    background: $color-background;
    color: $color-text;

    .header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 100%;
      padding-bottom: 10px;

      .header-title {
        font-size: 30px;
      }

      .header-menu {
        color: $color-text;
        font-size: 30px;
      }

      .menu-button {
        background: none;

        &:hover {
          background: $color-button-hover;
        }
      }

      .menu-list {
        border: none;
        background: $color-list;

        .menu-item {
          background: $color-list-item;

          .menu-logout-button {
            background: $color-list-item;
            height: 30px;
            width: 100%;

            &:hover {
              background: $color-button-hover;
            }
          }
        }
      }
    }
  }

  .body {
    background: $color-working;
  }
}
